.profileScreen {
  color: #fff;
  position: relative;
  height: 100vh;
  background-size: cover;
}

.profileScreen__body {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 8%;
}

.profileScreen__info {
  display: flex;
  right: auto;
}

.profileScreen__info > img {
  height: 100px;
}

.profileScreen__body > h2 {
  font-size: 40px;
  font-weight: 300;
  border-bottom: 1px solid #282c2d;
  margin-bottom: 20px;
}

.profileScreen__details {
  color: #fff;
  margin-left: 0;
  margin-right: 0;
  flex: 1;
}

.profileScreen__details > h2 {
  background-color: rgb(175, 175, 175);
  padding: 15px;
  font-size: 15px;
  padding-left: 20px;
}

.profileScreen__signOut {
  font-size: 1rem;
  padding: 10px 20px;
  margin-top: 5%;
  width: 100%;
  color: #fff;
  background-color: #e50914;
  font-weight: 600;
  border: none;
  cursor: pointer;
}

.profileScreen__plans {
  margin-top: 20px;
}

.profileScreen__plans > h3 {
  border-bottom: 1px solid #282c2d;
  padding-bottom: 10px;
}
/* for mobile screen */

@media only screen and (max-width: 1108px) and (min-width: 500px) {
}
@media screen and (max-width: 480px) {
  .profileScreen__body {
    font-size: 30px !important;
    align-items: left;
  }
}
