.signupScreen {
  max-width: 300px;
  padding: 70px;
  margin-left: auto;
  margin-right: auto;
  background: transparent;
}

.signupScreen > form > h1 {
  text-align: left;
  margin-bottom: 25px;
}
.signupScreen > form > h4 {
  text-align: left;
  margin-bottom: 30px;
}
.signupScreen_gray {
  color: rgb(230, 228, 228);
}

.signupScreen > form {
  display: grid;
  flex-direction: column;
}

.signupScreen > form > input {
  outline-width: 0;
  height: 40px;
  margin-bottom: 14px;
  border-radius: 5px;
  border: none;
  padding: 5px 15px;
}
.signupScreen > form > button {
  padding: 16px 20px;
  font-size: 1rem;
  color: white;
  border-radius: 5px;
  background-color: #e50914;
  font-weight: 600;
  border: none;
  cursor: pointer;
  margin-top: 20px;
}
.signupScreen_button:hover {
  cursor: pointer;
  text-decoration: underline;
  color: green;
}
.signupScreen_or {
  align-items: center;
  margin-bottom: 25px;
  padding-bottom: 10px;
}

/* for mobile screen*/
@media only screen and (max-width: 1108px) and (min-width: 500px) {
}

@media screen and (max-width: 500px) {
  .signupScreen {
    max-width: 100% !important;
  }
  .signupScreen > form > h1 {
    max-width: 100% !important;
  }
  .signupScreen > form > h4 {
    max-width: 100% !important;
  }
  .signupScreen > form > input {
    max-width: 100% !important;
  }
  .signupScreen > form {
    max-width: 100% !important;
    align-items: center;
  }
  .signupScreen_gray {
    max-width: 100% !important;
  }
  .signupScreen > form > button {
    max-width: 100% !important;
  }
  .signupScreen_button:hover {
    max-width: 100% !important;
  }
}
